@import url(https://fonts.googleapis.com/css2?family=Inter:wght@300;400;800&display=swap);
@import url(https://fonts.googleapis.com/css2?family=Inter:wght@300;400;800&display=swap);
@import url(https://fonts.googleapis.com/css2?family=Inter:wght@300;400;800&display=swap);
body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}

.sidebar{
    top: 60px;
    width: 350px;
    background: #000614;
    color: white;
    min-height: 66vh;
    border-radius: 0 0 10px 0;
    position:fixed;
    
}

.Dropdown{
    display: none;
}

.sidebar-sel{
    background: #59219D;
    border-radius: 17px;
}

.coin{
    padding: 0.5rem 1rem;
    font-weight: bold;
    cursor: pointer;
    margin: 1rem;
    font-size: 13px;
}
@media screen and (max-width:766px) and (min-width:270px) {
    .sidebar{
        display: none;
    }   
    .Dropdown{

        display: inline-block;
        margin-top: 80px;
        width: 100%;
        padding: 0 10px;
        display: inline-block;
        margin-bottom: 5px;
    }
    .Dropdown button{
        background: #59219D;
        width: 100%;
        height: 40px;
        /* margin: 0px; */
        border-radius: 12px;
        text-align: left;
    }
    .dropdown-menu{
        width: 100%;
        background: #51307a;
        color: white;
        border-radius: 10px;
    }
    .dropdown-item{
        color: white;
    }
    .btn:active, .btn:focus, .btn:hover,.btn,.btn-primary {
        background-color: #59219D !important;
        border-color: #59219D !important;
        box-shadow: none;
    }
    .dropdown-toggle::after {
        display: none !important;
    }
    
    
}
input::-webkit-outer-spin-button,
input::-webkit-inner-spin-button {
  -webkit-appearance: none;
  margin: 0;
}

/* Firefox */
input[type=number] {
  -moz-appearance: textfield;
}
